<template>
	<view class="form-box">
		<!-- 手机号 -->
		<view class="form-item-box">
			<!-- 区号选择器 -->
			<picker @change="handlePullPhoneList" :range="listItem" :value="index" >
				<!-- picker里面只能有一个子元素 -->
				<view class="picker-box">
					<view class="phone-title-box">
						<view class="phone-title">{{ listItem[index] }}</view>
						<view class="triangle-icon">
							<image src="../../static/img/triangle.png" mode="widthFix"></image>
						</view>
					</view>
					<input @input="handleInput" type="number" :placeholder="accountPlaceHolder" placeholder-style="color:#D4D9E7" />
				</view>
			</picker>
		</view>
		<!-- 密码 -->
		<view class="form-item-box password-input">
			<view>
				<!-- 显示密码 -->
				<input 
				type="text" 
				v-if="isShowPassword" 
				:placeholder="passwordPlaceHolder"
				placeholder-style="color:#D4D9E7" />
				<!-- 不显示密码 -->
				<input 
				type="text" 
				password 
				v-else 
				:placeholder="passwordPlaceHolder" 
				placeholder-style="color:#D4D9E7" />
			</view>
			<view class="see-icon-box">
				<image 
				:src="isShowPassword?'../../static/img/eye-o.png':'../../static/img/eye.png'" 
				@click="showPassword"
				 mode="widthFix"></image>
			</view>
		</view>
		<!-- 按钮 -->
		<button>{{ i18n.signIn }}</button>
	</view>
</template>

<script>
	import indexList from '@/utils/country.js'
	export default {
		data() {
			return {
				// 区号
				phoneTittle: '+65',
				// 区号数据
				listItem: indexList.list,
				index: 0,
				// 是否显示密码
				isShowPassword: false
			}
		},
		methods: {
			// 点击区号
			handlePullPhoneList(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value
			},
			// 选择区号
			handleChoosePhoneTitle(item) {
				this.phoneTittle = item.code
				this.phoneTitleShow = !this.phoneTitleShow
			},
			// 点击眼睛切换密码显示
			showPassword(){
				this.isShowPassword = !this.isShowPassword
			}
		},
		computed: {
			i18n() {
				return this.$t('login')
			},
			accountPlaceHolder() {
				return this.$t('login.accountPlaceHolder')
			},
			passwordPlaceHolder() {
				return this.$t('login.passwordPlaceHolder')
			}
		}
	}
</script>

<style lang="scss" scoped>
	.form-box {
		margin-top: 160rpx;
		padding: 0 80rpx;
		.form-item-box {
			height: 94rpx;
			background-color: #f9fcff;
			border-radius: 20rpx;
			display: flex;
			align-items: center;
			padding: 0 20rpx;
			.picker-box{
				width: 100%;
				display: flex;
				align-items: center;
				.phone-title-box{
					display: flex;
					align-items: center;
					.phone-title{
						font-size: 26rpx;
						font-weight: bold;
						color: #23252B;
						margin-right: 8rpx;
					}
					.triangle-icon{
						width: 10rpx;
						height: 9rpx;
						margin-right: 30rpx;
						image{
							height: 100%;
						}
					}
				}
			}
			input {
				height: 94rpx;
			}
			.see-icon-box{
				width: 34rpx;
				height: 23rpx;
				image{
					height: 100%;
				}
			}
		}
		.form-item-box:first-of-type {
			margin-bottom: 70rpx;
		}
		.password-input{
			justify-content: space-between;
			padding-right: 30rpx;
		}
	}
	button {
		@include btn-base;
		margin-top: 80rpx;
	}
	.placeholder{
		color: red!important;
	}
</style>
